/*---
 section: Forms
 title: Dropdowns
 ---

 ```html_example
 <div class="c-dropdown">
   <input type="checkbox" id="dropdown-1">
   <label for="dropdown-1" class="c-dropdown__toggle">Option 1 <span class="caret"></span></label>
   <ul class="c-dropdown__content" id="test-dropdown" tabindex="-1" aria-hidden="true">
     <li><a href="#">Option 1</a></li>
     <li><a href="#">Option 2</a></li>
   </ul>
 </div>
 ```

 */

:root {
  --callout-bg-color: var(--white);
  --dropdown-width: 144px;
}

.c-dropdown {
  height: 30px;
  position: relative;

  input[type="checkbox"] {
    opacity: 0;
    position: absolute;

    &:checked {
      + label + .c-dropdown__content {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &__toggle {
    background-color: var(--white);
    border-radius: 4px;
    border: 1px solid var(--grey-600);
    color: rgba(0, 0, 0, .75);
    cursor: pointer;
    display: inline-block;
    font-size: 18px !important;
    font-weight: normal !important;
    margin: 0;
    padding: 2px 5px;
    position: relative;
    text-align: left;
    min-width: calc(var(--dropdown-width) - 44px);

    @media (--md-up) {
      min-width: var(--dropdown-width);
    }

    &::before {
      @mixin triangle 'bottom', var(--black), 5px;
      position: absolute;
      right: 15px;
      top: 11px;
    }

    @media (--md-up) {
      font-size: 16px !important;
    }
  }

  &__content {
    background-color: var(--callout-bg-color);
    box-shadow: var(--grey-600) 0 0 7px;
    border-radius: 4px;
    color: rgba(0, 0, 0, .75);
    list-style: none;
    margin: -.8rem 0 0;
    opacity: 0;
    padding: 3px;
    pointer-events: none;
    position: relative;
    transition-duration: var(--global-duration);
    min-width: calc(var(--dropdown-width) - 44px);

    @media (--md-up) {
      min-width: var(--dropdown-width);
    }

    a {
      color: var(--black) !important;
      border-radius: 4px;
      display: block;
      font-size: 18px;
      padding: 5px;
      transition-duration: var(--global-duration);

      &:hover {
        background-color: var(--grey-300);
        text-decoration: none !important;
      }

      @media (--md-up) {
        font-size: 16px;
      }
    }
  }
}
