/*---
 section: Utilities
 title: Classes
 ---
*/

/* Box */

%clearfix {
  &::before,
  &::after {
    content: ' ';
    display: table;
  }

  &::after { clear: both; }
}

%block { display: block; }


.u-reset-box,
%reset-box {
  border: 0;
  margin: 0;
  padding: 0;
}

%reset-mobile-order {
  @media (--sm) {
    order: 0 !important;
  }
}

%spacer { flex: auto; }

.u-right, .u-right--clear, %right { float: right; }
.u-right--clear { clear: both; }
.u-left, .u-left--clear, %left { float: left; }
.u-left--clear { clear: both; }

/* Lists */

%no-bullet {
  list-style-image: none;
  list-style-type: none;
  margin-left: 0;
}

%no-bullets {
  list-style: none;
  li { @extend %no-bullet; }
}

%list-horizontal {
  @extend %reset-box;
  @extend %clearfix;
}

.u-list-inline,
%list-inline {
  @extend %no-bullets;
  margin-top: 0;
  margin-bottom: .5em;
  margin-left: .5em;
  overflow: hidden;
  padding: 0;

  > li {
    display: block;
    float: left;
    margin-left: 1em;

    > * { display: block; }
  }
}

/* Positioning */

%sticky-top {
  @mixin plank;
  top: 0;
}

%sticky-bottom {
  @mixin plank;
  bottom: 0;
}

%absolute-top {
  @mixin plank absolute;
  top: 0;
}

%absolute-bottom {
  @mixin plank absolute;
  bottom: 0;
}

/* Visibility */
.u-hidden,
%hidden { display: none !important; }

.u-show,
%show { display: inherit !important; }

@media print {
  .u-hidden\@print { @extend %hidden; }
}

@media (--sm) {
  .u-hidden\@sm { @extend %hidden; }
  .u-show\@sm { @extend %show; }
  .u-show\@md { @extend %hidden; }
}

@media (--sm-up) { .u-hidden\@sm-up { @extend %hidden; } }

@media (--md) {
  .u-hidden\@md { @extend %hidden; }
  .u-show\@md { @extend %show; }
}

@media (--md-up) {
  .u-hidden\@md-up { @extend %hidden; }
  .u-show\@sm { @extend %hidden; }
}

@media (--md-down) {
  .u-show\@lg { @extend %hidden; }
}

@media (--lg) {
  .u-hidden\@lg { @extend %hidden; }
}

@media (--lg-up) {
  .u-hidden\@lg-up { @extend %hidden; }
  .u-show\@lg { @extend %show; }
  .u-show\@md { @extend %hidden; }
}

/* stylelint-disable */
%appearance {
  -moz-appearance: none;
  -webkit-appearance: none;
}

%user-select {
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

%assert-selector {
  outline: 2px solid var(--red-600);
}
/* styleguide-enable */

