/*---
 section: Containers
 title: Modal
 ---

 ```html_example
 <a href="#test-modal" class="c-button">Open Modal</a>

 <div class="c-modal" id="test-modal">
   <div class="c-modal__content">
     <div class="c-modal__card">
       <div class="c-modal__title">Modal Title <a href="#close"></a></div>
       <p>This is a card in a modal, what will they think of next?</p>
       <div class="c-modal__actions">
         <a href="#close" class="c-button--alert">Close</a>
       </div>
     </div>
   </div>
   <a href="#close" class="c-modal__close"></a>
 </div>
 ```
 */

.c-modal {
  &__close {
    background: rgba(0, 0, 0, .6);
    content: '';
    opacity: 0;
    position: fixed;
    transition-duration: var(--global-duration);
    z-index: 400;
  }

  &:target {
    .c-modal__close {
      bottom: 0;
      left: 0;
      opacity: 1;
      right: 0;
      top: 0;
    }
    .c-modal__content {
      @extend %row;
      position: fixed;
      left: 0;
      right: 0;
      top: 30%;
      opacity: 1;
      z-index: 500;
    }
  }

  .c-modal__content {
    position: fixed;
    opacity: 0;
    transition-duration: var(--global-duration);
  }

  &__card {
    @mixin card var(--grey-100);
  }

  &__title {
    @extend %row-break;
    margin-top: -.5em;
    padding: .5rem 1rem;
    margin-bottom: .5rem;
    font-size: 1.3rem;
    background-color: var(--blue-800);
    color: var(--white);
  }

  &__actions {
    @extend %right;
  }
}


#close {
  display: none;
}
