/*---
 section: Containers
 title: Card
 ---

 ```html_example
 <div class="c-card">
   <h3>Card Title</h3>
   <p>I could have used lorem ipsum, but what's the fun in that?</p>
 </div>
 ```

 ### Customize

 ``` css
  --card-background-color: var(--white);
  ```
 */

:root {
  --card-background-color: var(--white);
}

@define-mixin card $background: var(--card-background-color) {
  background-color: $background;
  box-shadow: 0 0 10px rgba(0, 0, 0, .4);
  margin: 1em auto 0;
  padding: .5rem 1rem;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: 10px;
  }
}

.c-card {
  @mixin card;
}
