/*---
 section: Base
 title: Layout
 ---

 The main layout depends on the `.s-app` class to render a holy grail layout that scales down nicely to mobile.

 <div class="u-column--6@md">
 <img src="dist/img/layout-holy-grail.svg" alt="">
 </div>

 [Mozilla - Holy Grail Layout](https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Flexible_Box_Layout/Using_CSS_flexible_boxes#Holy_Grail_Layout_example)
 */

.s-app {
  height: 100%;

  &,
  body {
    box-sizing: border-box;
    font-size: 100%;
    height: 100%;
    overflow-x: hidden;
    width: 100%;
  }

  body {
    @extend %reset-box;
    @extend %layout;
    margin-top: 58px; /* combined height of .o-topbar */
  }

}

/** @define t-default */

.t-default {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: var(--font-family);
  font-weight: var(--font-weight);
}

/** @define o-main */

%main {
  @extend %container-fluid;
  height: calc(100% - 58px);
  min-height: 500px;
  overflow-x: hidden;

  @media (--sm) {
    flex-direction: column;
  }
}

.o-main {
  @extend %main;

  &--narrow {
    @extend %main;
    margin-left: auto;
    margin-right: auto;
    max-width: 500px;
  }


  > aside {
    @extend %reset-mobile-order;
    flex: 1 6 20%;
    order: 1;

    @media (--sm) {
      min-height: 50px;
      max-height: 50px;
    }
  }

  > .o-article {
    @extend %reset-mobile-order;
    flex: 2 1 80%;
    order: 2;
  }

}

