:root {
  --font-family: 'Source Sans Pro', sans-serif;
  --font-weight: 400;
  --font-weight-bold: 600;
  --h-margin: 1em 0 .25em;
}

%link-hover {
  text-decoration: none;
  &:hover,
  &:focus { text-decoration: underline; }
}

%link-unstyled {
  color: inherit;
  text-decoration: inherit;
  cursor: inherit;
  &:active,
  &:focus {
    outline: none;
  }
}

%h-base-styles {
  margin: var(--h-margin);
}

@define-mixin link-colors $normal, $hover, $active, $visited, $focus {
  color: $normal;
  &:visited { color: $visited; }
  &:focus { color: $focus; }
  &:hover { color: $hover; }
  &:active { color: $active; }
}


.u-fancy,
%fancy {
  color: var(--blue-600);
  font-weight: 300 !important;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}

.u-loud,
%loud {
  color: var(--black);
  font-weight: 700 !important;
}

.u-soft,
%soft {
  color: var(--grey-600);
  font-weight: 300 !important;
}

.u-intro,
%intro {
  color: var(--red-800);
  font-size: 2.2rem;
  font-weight: 700 !important;
  line-height: 1.14;
  margin-bottom: 1rem;
}

.t-default {
  margin: 0;

  h1,
  h2,
  h3, {
    @extend %h-base-styles;
    font-weight: var(--font-weight);
  }

  h4,
  h5,
  h6 {
    @extend %h-base-styles;
    font-weight: var(--font-weight-bold);
  }

  h1 { font-size: 48px; }
  h2 { font-size: 40px; }
  h3 { font-size: 31px; }
  h4 { font-size: 25px; }
  h5 { font-size: 20px; }
  h6 { font-size: 16px; }

  a:not([class]) {
    @extend %link-hover;
    color: var(--blue-600);
    text-decoration: none;
  }

  p { margin-bottom: 1em; }
}
