/*---
 section: Forms
 title: Buttons
 ---

 Use the button classes on an `<a>`, `<button>`, `<input>` element. It's easy to make a new button.

 ```html_example
 <button class="c-button">Default</button>
```

 #### Size and Form

 You can optionally use modifier classes that let you manipulate the size and shape of your button.

 ``` html_example
 <button class="c-button">Default</button>
 <button class="c-button c-button--round">Round</button>
 <button class="c-button c-button--small">Small</button>
 <button class="c-button c-button--large">Large</button>
 <button class="c-button c-button--wide">Wide</button>
 ```

 #### Color

 Stateful color classes are provided to further communicate the intentions of your button action.

 ``` html_example
 <button class="c-button">Default</button>
 <button class="c-button--secondary">Secondary</button>
 <button class="c-button--alert">Alert</button>
 <button class="c-button--info">Info</button>
 <button class="c-button--link">Link</button>
 <button class="c-button--anchor">Anchor</button>
 ```

 #### States

 ``` html_example
 <button class="c-button">Default</button>
 <button class="c-button" disabled>Disabled</button>
 <button class="c-button" title="Click me to see the active state.">Active</button>
 ```

 #### Icons

 ```html_example
 <button class="c-button"><i class="o-icon-email"></i> Email</button>
 <button class="c-button"><i class="o-icon-inbox"></i> Inbox</button>
 ```

 #### Customize

 ``` css
 --button-background: var(--orange-600);
 --button-background-hover: var(--orange-500);
 --button-color: contrast(var(--button-background));
 --button-disabled-opacity: .35;
 --button-font-size: 1.125rem;
 --button-font-size-sm: calc(var(--button-font-size) * .75);
 --button-font-size-lg: calc(var(--button-font-size) * 1.25);
 --button-margin: var(--global-margin);
 --button-padding: .7em 1.5em;
 --button-round: var(--global-rounded);
 ```

 #### Mixins

 `@mixin button var(--button-background), var(--button-color), var(--button-font-size); `
*/

:root {
  --button-background: var(--blue-800);
  --button-background-hover: var(--blue-700);
  --button-color: contrast(var(--button-background));
  --button-disabled-opacity: .35;
  --button-font-size: 1.125rem;
  --button-font-size-sm: calc(var(--button-font-size) * .75);
  --button-font-size-lg: calc(var(--button-font-size) * 1.25);
  --button-margin: var(--global-margin);
  --button-padding: .7em 1.5em;
  --button-round: var(--global-rounded);
}


/* Mixins */

@define-mixin button $background: var(--button-background), $background-hover: var(--button-background-hover), $font-size: var(--button-font-size) {

  background-color: $background;
  box-shadow: 0 0 0 var(--shadow-color);
  color: contrast($background);
  font-size: $font-size;

  &:hover,
  &:focus {
    background-color: $background-hover;
    box-shadow: 0 0 6px var(--shadow-color);
    text-decoration: none;
  }
}

@define-mixin button-group {
  @extend %clearfix;
  @extend %reset-box;
  list-style: none;

  .c-button {
    float: left;
    font-size: inherit;
    margin: 0 0 .5em;
  }
}

@define-mixin button-group-vertical {
  @mixin button-group;

  [class ^="c-button"] {
    width: 100%;
  }
}

/* Placeholder Selectors */

%button-active {
  transform: scale(.95);
}

%button {
  @extend %user-select;
  @extend %appearance;
  border-radius: var(--button-round);
  cursor: pointer;
  display: inline-block;
  padding: var(--button-padding);
  position: relative;
  line-height: 1;
  margin: 0 var(--button-margin) var(--button-margin) 0;
  min-width: 75px;
  text-align: center;
  transition: all var(--global-duration) ease-out;
  vertical-align: middle;

  &:active {
    @extend %button-active;
  }

  &:focus {
    outline: 0;
  }

  [class*='dir'] {
    position: relative;
    top: 2px;
  }

  &:not(button, [type=submit]) {
    @extend %assert-selector;
  }

}

%button-base {
  @mixin button;
}


/* Classes */

.c-button {
  @extend %button;
  @extend %button-base;

  &--secondary {
    @extend %button;
    @mixin button var(--orange-600), var(--orange-500);
  }

  &--alert {
    @extend %button;
    @mixin button var(--red-700), var(--red-600);
  }

  &--info {
    @extend %button;
    @mixin button var(--white), var(--blue-200);
    border: var(--grey-600) 1px solid;
  }

  &--anchor {
    @extend %button;
    @mixin button transparent, transparent, 12px;
    color: var(--blue-700);
    text-transform: uppercase;
    &:hover,
    &:focus {
      color: var(--blue-600);
      box-shadow: none;
      text-decoration: underline;
    }
  }

  /* TODO Make this more official */
  &--ghost, &--link {
    @extend %button;
    @mixin button var(--background-color), var(--background-color);
    border: 1px solid transparent;
    &:hover,
    &:active {
      border: 1px solid var(--grey-300);
      box-shadow: 0 0 3px var(--grey-300);
    }
  }

  &--round { border-radius: 50px !important; }
  &--small { font-size: var(--button-font-size-sm) !important; }
  &--large { font-size: var(--button-font-size-lg) !important; }

  &--wide {
    @extend %text-center;
    width: 100%;
  }

  @media (--sm) {
    &--wide\@sm {
      @extend %text-center;
      width: 100%;
    }
  }
}

.t-default {
  button[disabled] {
    opacity: var(--button-disabled-opacity);
    pointer-events: none;
  }
}


/* Inbox */
.c-button-group { @mixin button-group; }
.c-button-group--vertical { @mixin button-group-vertical; }

.o-actions {
  margin: 3em 0;
  overflow: hidden;
}
