/* stylelint-disable rule-properties-order */

:root {
  --blue-50: #f0faff;
  --blue-100: #cef;
  --blue-200: #aaddfa;
  --blue-300: #94d8ff;
  --blue-400: #85cce5;
  --blue-500: #55bee5;
  --blue-600: #00a0d0;
  --blue-700: #1c7dab;
  --blue-800: #047;
  --blue-900: #0d2c54;
  --blue-1000: #024;
  --blue-1100: #002;

  --orange-500: #f92;
  --orange-600: #f07700;

  --grey-50: #fcfcf9;
  --grey-100: #eeeee7;
  --grey-300: #ddddd4;
  --grey-500: #ccccc2;
  --grey-600: #a3a396;
  --grey-700: #77776d;
  --grey-800: #33332a;
  --black: #000;
  --white: #fff;

  --red-50: #fbb;
  --red-600: #d03000;
  --red-700: #900;
  --red-800: #800;

  --gold-50: #feb;
  --gold-200: #ffd666;
  --gold-300: #ffc838;
  --gold-500: #ffba0a;
  --gold-700: #eeb111;

  --green-50: #b6f5e3;
  --green-600: #09b081;
  --green-700: #00996e;
  --green-800: #007a56;

  --purple-a100: #831dcb;
  --magenta-500: #d95db7;

  --brand-blue: var(--blue-800);
  --brand-orange: var(--orange-600);

  --shadow-color: rgba(51, 51, 42, .6);

  /* Default Theme Color Pallet */
  --background-color: var(--grey-50);
  --text-color: var(--grey-800);
  --primary-color: var(--blue-800);
  --secondary-color: var(--blue-400);
}

/* stylelint-enable */

@define-mixin color-class $color {
  background: $color !important;
  color: contrast($color);
}

.u-blue-50 { @mixin color-class var(--blue-50); }
.u-blue-100 { @mixin color-class var(--blue-100); }
.u-blue-200 { @mixin color-class var(--blue-200); }
.u-blue-300 { @mixin color-class var(--blue-300); }
.u-blue-400 { @mixin color-class var(--blue-400); }
.u-blue-500 { @mixin color-class var(--blue-500); }
.u-blue-600 { @mixin color-class var(--blue-600); }
.u-blue-700 { @mixin color-class var(--blue-700); }
.u-blue-800 { @mixin color-class var(--blue-800); }
.u-blue-900 { @mixin color-class var(--blue-900); }
.u-blue-1000 { @mixin color-class var(--blue-1000); }
.u-blue-1100 { @mixin color-class var(--blue-1100); }

.u-grey-50 { @mixin color-class var(--grey-50); }
.u-grey-100 { @mixin color-class var(--grey-100); }
.u-grey-300 { @mixin color-class var(--grey-300); }
.u-grey-500 { @mixin color-class var(--grey-500); }
.u-grey-600 { @mixin color-class var(--grey-600); }
.u-grey-700 { @mixin color-class var(--grey-700); }
.u-grey-800 { @mixin color-class var(--grey-800); }
.u-black { @mixin color-class var(--black); }
.u-white {
  border: 1px solid var(--black);
  @mixin color-class var(--white);
}

.u-orange-500 { @mixin color-class var(--orange-500); }
