/*---
 section: Forms
 title: Elements
 ---

 ### Inputs

 Supported input types are, `text`, `password`, `date`, `datetime`, `datetime-local`, `month`, `week`, `email`, `number`, `search`, `tel`, `time`, `url`, `color`.

 ```html_example
 <label for="input-1-1">Label</label>
 <input type="text" id="input-1-1">

 <label for="input-1-2">Focus</label>
 <input type="text" id="input-1-2" class="focus">

 <div class="c-input-group">
   <label for="input-2-1">Warning</label>
   <input type="email" id="input-2-1" value="notauser at clientcom" required>
   <div class="c-validation--warning">Warning validated message</div>
 </div>

 <div class="c-input-group">
   <label for="input-2-2">Error</label>
   <input type="text" id="input-2-2" class="error">
   <div class="c-validation--error" placeholder="Placeholder text">Error validated message</div>
 </div>
 ```

 ### Textarea

 ```html_example
 <label for="textarea-1-1">Textarea</label>
 <textarea id="textarea-1-1"></textarea>
 ```

 ### Checkboxes

 ```html_example
   <input type="checkbox" class="c-checkbox"></input>
   <label>Checkbox</label>
   <br>
   <input type="checkbox" class="c-checkbox" checked></input>
   <label>Checked Checkbox</label>
 ```

 */

:root {
  --input-font-size: 18px;
  --textarea-font-size: 16px;
}

@define-mixin validation $color {
  border: 1px solid $color;
  box-shadow: 0 0 6px $color;
  outline: 0;
}

%base-input {
  background-color: var(--white);
  border-radius: 4px;
  border: 1px solid var(--grey-600);
  color: contrast(var(--white));
  display: block;
  margin: 0 0 1em 0;
  padding: 5px;
  width: 100%;
}

.t-default {
  input:not([type]),
  input[type="text"],
  input[type="password"],
  input[type="date"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="month"],
  input[type="week"],
  input[type="email"],
  input[type="number"],
  input[type="search"],
  input[type="tel"],
  input[type="time"],
  input[type="url"],
  input[type="color"] {
    @extend %base-input;
    font-size: var(--input-font-size);

    @media (--md-up) {
      font-size: calc(var(--input-font-size) - 2px);
    }
  }

  textarea {
    @extend %base-input;
    font-size: var(--textarea-font-size);
    resize: none;
    overflow: auto;
    outline: none;

    @media (--sm) {
      height: 40vh;
    }

    @media (--md-up) {
      font-size: calc(var(--textarea-font-size) - 2px);
    }
  }

  input {
    transition-duration: var(--global-duration);
    position: relative;

    &:focus,
    &.focus {
      @mixin validation var(--blue-600);
    }

    &:invalid,
    &.invalid {
      @mixin validation var(--orange-600);
    }

    &.error {
      @mixin validation var(--red-600);
    }

  }

  label {
    display: block;
    color: var(--black);
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 1em;
  }
}

/** @define c-input-group */

.c-input-group {
  position: relative;

  .u-right {
    cursor: pointer;
    height: 15px;
    padding: 8px 0;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    transition-duration: var(--global-duration);
    width: 40px;
  }

  input[type="text"] + .u-right {
    /* color: var(--orange); */
  }
}

/** @define c-checkbox */

.c-checkbox {
  display: none;
  font-size: 12px;

  & + label {
    position: relative;
    font-weight: normal;

    &::before {
      background-color: var(--white);
      border: 2px solid var(--grey-700);
      border-radius: 4px;
      content: '';
      display: inline-block;
      float: left;
      font-size: 14px;
      margin-bottom: 0;
      margin-right: 8px;
      margin-top: 3px;
      padding: 6px;
      position: relative;
    }

    &::after {
      color: var(--white);
      content: '\2714';
      font-family: 'navis-icons';
      font-size: 11px;
      left: 3px;
      opacity: 0;
      position: absolute;
      top: 2px;
      transition-duration: var(--global-duration);
      z-index: 5;
    }
  }

  & + label:active,
  &:checked + label:active {
    box-shadow: 0 1px 2px rgba(0, 0, 0, .05), inset 0 1px 3px rgba(0, 0, 0, .1);
  }

  &:checked + label {
    &::before {
      background-color: var(--green-600);
      border: 2px solid var(--green-600);
      color: #99a1a7;
    }

    &::after {
      opacity: 1;
    }
  }
}

/** @define o-link */

.o-link {
  &--alt {
    color: var(--blue-700);
    font-size: 12px;
    margin-right: 10px;
    text-transform: uppercase;
  }
}


%validation {
  @extend %user-select;
  border-radius: 0 0 var(--global-rounded) var(--global-rounded);
  font-size: 12px;
  margin-bottom: 1em;
  margin-top: calc(-1em - 6px);
  padding: 3px 7px;
  transition-duration: var(--global-duration);
}

.c-validation {
  @extend %validation;

  &--warning {
    @extend %validation;
    background-color: var(--gold-50);
    color: contrast(var(--gold-50));
  }

  &--error {
    @extend %validation;
    background-color: var(--red-600);
    color: contrast(var(--red-600));
  }
}

