/*---
 section: Utilities
 title: Mixins
 ---

 #### List Item Horizontal

 ```css
 @mixin list-item-horizontal $padding, $direction;
 ```

 #### Image Bullets

 ```css
 @mixin image-bullets $icon, $width, $height, $line-height;
 ```
 #### Triangle

 ```css
 @mixin triangle $direction, $color, $size;
 ```

 <div class="arrow"></div>

 #### Plank

 ```css
 @mixin plank $type;
 ```

 #### Sticky Footer

 ```css
 @mixin sticky-footer $height;
 ```
*/

/* stylelint-disable */
@define-mixin list-item-horizontal $padding: 4px, $direction: left {
  @extend %no-bullets;
  white-space: nowrap;
  /* @mixin float $direction;  */
  padding-left: $padding;
  padding-right: $padding;
  /* todo */
}

@define-mixin image-bullets $icon: resolve($icon), $width: width($icon), $height: height($icon), $line-height: 18px, $padding: 14px {
  margin-left: 0;
  li {
    padding-left: $padding;
    background: $icon no-repeat ($padding - $width) / 2 ($line-height - $height) / 2;
    list-style-type: none;
  }
}

@define-mixin triangle $direction, $color: currentColor, $size: 1em {

  $perpendicular-borders: $size solid transparent;

  @if $direction == right { border-left: calc($size * 1.5) solid $color; }
  @if $direction == top { border-bottom: calc($size * 1.5) solid $color; }
  @if $direction == bottom { border-top: calc($size * 1.5) solid $color; }
  @if $direction == left { border-right: calc($size * 1.5) solid $color; }
  @if $direction == top or $direction == bottom {
    border-left: $perpendicular-borders;
    border-right: $perpendicular-borders;
  } @else if $direction == right or $direction == left {
    border-bottom: $perpendicular-borders;
    border-top: $perpendicular-borders;
  }
  content: '';
  height: 0;
  width: 0;
  z-index: 2;

}

@define-mixin plank $type: fixed {
  left: 0;
  position: $type;
  right: 0;
}

@define-mixin sticky-footer $height {
  .s-app-container {
    min-height: 100%;
    height: auto !important;
    height: 100%;
    margin-bottom: -$height;
    .o-container {
      padding: 0 0 $height 0;
    }
  }

  .o-footer {
    height: $height;
  }
}

/* stylelint-enable */

