/*---
 section: Containers
 title: Tooltips
 ---

 ```html_example
 <a class="c-button c-tooltip c-tooltip--top" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Top tooltip</a>
 <a class="c-button c-tooltip c-tooltip--right" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Right tooltip</a>
 <a class="c-button c-tooltip c-tooltip--bottom" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Bottom tooltip</a>
 <a class="c-button c-tooltip c-tooltip--left" data-tooltip="Tooltip content goes in a data-tooltip attribute.">Left tooltip</a>
 ```

 ### Customize

 ``` css
  --tooltip-arrow-size: .375rem;
  --tooltip-background: var(--white);
  --tooltip-margin: .5rem;
  --tooltip-zindex: 1000;
  ```

 */

:root {
  --tooltip-arrow-size: .375rem;
  --tooltip-background: var(--white);
  --tooltip-margin: .5rem;
  --tooltip-zindex: 1000;
}

.c-tooltip {
  position: relative;
  display: inline-block;
  transform: translate3d(0, 0, 0);

  &::before,
  &::after {
    position: absolute;
    pointer-events: none;
    opacity: 0;
    transition: var(--global-duration) ease;
    visibility: hidden;
    z-index: var(--tooltip-zindex);;
  }

  &--visible,
  &:hover,
  &:focus {
    &:before,
    &:after {
      visibility: visible;
      opacity: 1;
    }
  }

  &::before {
    content: attr(data-tooltip);
    font-size: .83333rem;
    line-height: 1.5625rem;
    margin: 0;
    padding: 0 .5rem;
    color: contrast(var(--tooltip-background));
    background: var(--tooltip-background);
    box-shadow: 0 4px 8px rgba(0, 0, 0, .3);
    border-radius: 2px;
  }

  &::after {
    content: "";
    z-index: calc(var(--tooltip-zindex) + 1);
    position: absolute;
    border: var(--tooltip-arrow-size) solid transparent;
    background: transparent;
  }

  /* Positions */

  &--top,
  &--right,
  &--bottom,
  &--left {
    &::before,
    &::after {
      transform: translateX(-50%);
    }
    &::after {
      margin-bottom: calc(var(--tooltip-arrow-size) * -2);
      border-top-color: var(--tooltip-background);
    }
  }

  &--visible.tooltip--top,
  &--visible.tooltip--right,
  &--visible.tooltip--bottom,
  &--visible.tooltip--left,
  &--top:hover,
  &--right:hover,
  &--bottom:hover,
  &--left:hover,
  &--top:focus,
  &--right:focus,
  &--bottom:focus,
  &--left:focus {
    &::before {
      margin-bottom: calc(var(--tooltip-arrow-size) * 1 + var(--tooltip-margin));
    }

    &::after {
      margin-bottom: calc(var(--tooltip-arrow-size) * -1 + var(--tooltip-margin));
    }
  }

  &--top {
    &::before,
    &::after {
      bottom: 100%;
      left: 50%;
    }
  }

  &--right {
    &::before,
    &::after {
      left: 100%;
      top: 50%;
    }
  }

  &--bottom {
    &::before,
    &::after {
      left: 50%;
      top: 100%;
    }
  }

  &--left {
    &::before,
    &::after {
      right: 100%;
      top: 50%;
    }
  }
}
