/*---
 section: Containers
 title: Notification
 ---

 A floating card that communicates a notification message.

 ### States

 Note: This default state gets manual control over what icon is shown.

 ```html_example
 <div class="c-notification">
   <i class="c-notification__icon o-icon-info-circled"></i>
   <div class="c-notification__content">
     <h6>Default Notification</h6>
     <p>Communicate a meaningful message.</p>
   </div>
   <i class="c-notification__cancel"></i>
 </div>
 ```

 ```html_example
 <div class="c-notification--success">
   <i class="c-notification__icon"></i>
   <div class="c-notification__content">
     <h6>Successful Notification</h6>
     <p>Communicate a meaningful message.</p>
   </div>
   <i class="c-notification__cancel"></i>
 </div>
 ```

 ```html_example
 <div class="c-notification--warning">
   <i class="c-notification__icon"></i>
   <div class="c-notification__content">
     <h6>Warning Notification</h6>
     <p>Communicate a meaningful message.</p>
   </div>
   <i class="c-notification__cancel"></i>
 </div>
 ```

 ```html_example
 <div class="c-notification--error">
   <i class="c-notification__icon"></i>
   <div class="c-notification__content">
     <h6>Error Notification</h6>
     <p>Communicate a meaningful message.</p>
   </div>
   <i class="c-notification__cancel"></i>
 </div>
 ```

 ### Positions

 You can apply modifier classes to your notification to pin them to different areas around the viewport.

 ###### CLASSES

 ```
 .c-notification--top-left
 .c-notification--top
 .c-notification--top-right
 .c-notification--bottom-right
 .c-notification--bottom
 .c-notification--bottom-left
 ```

 */

:root {
  --notification-margin: calc(var(--global-outter-margin) -.5rem);
  --notification-width: 400px;
  --notification-color: blue;
  --notification-success-color: green;
  --notification-warning-color: gold;
  --notification-error-color: gold;
}

%notification {
  @extend %row;
  padding: .5rem !important;
  text-align: left;
  transition-duration: var(--global-duration);

  @media (--md-up) {
    width: var(--notification-width);
  }
}

%notification-fixed {
  margin: 0 auto !important;
  position: fixed;
  z-index: 200;
}

.c-notification {
  @extend %notification;
  @mixin card;

  /* Positioning */

  &--top {
    @extend %notification-fixed;
    border-radius: 0 0 var(--global-rounded) var(--global-rounded);
    left: var(--notification-margin);
    right: var(--notification-margin);
    top: 0;
  }

  &--top-right {
    @extend %notification-fixed;
    right: var(--notification-margin);
    top: var(--notification-margin);
  }

  &--top-left {
    @extend %notification-fixed;
    left: var(--notification-margin);
    top: var(--notification-margin);
  }

  &--bottom {
    @extend %notification-fixed;
    border-radius: var(--global-rounded) var(--global-rounded) 0 0;
    bottom: 0;
    left: var(--notification-margin);
    right: var(--notification-margin);
  }

  &--bottom-right {
    @extend %notification-fixed;
    bottom: var(--notification-margin);
    right: var(--notification-margin);
  }

  &--bottom-left {
    @extend %notification-fixed;
    bottom: var(--notification-margin);
    left: var(--notification-margin);
  }

  /* States */

  &--success {
    @extend %notification;
    @mixin card;
  }

  &--warning {
    @extend %notification;
    @mixin card var(--gold-50);
  }

  &--error {
    @extend %notification;
    @mixin card var(--gold-50);
  }

  &__content {
    @extend %column-11;
    max-width: var(--notification-width);

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-size: 1rem;
      font-weight: bold;
      margin-bottom: 0;
      margin-top: 5px;
    }

    p {
      font-size: .9rem;
    }
  }

  &__icon {
    @extend %column-1;
    color: var(--blue-700);
    margin-top: .45rem;

    &::before { @extend %icon; }

    .c-notification--success & {
      color: var(--green-700);

      &::before { @extend %icon-check-mark-circle; }
    }

    .c-notification--warning & {
      color: var(--orange-600);

      &::before { @extend %icon-exclamation-triangle; }
    }

    .c-notification--error & {
      color: var(--red-700);

      &::before { @extend %icon-x-mark-circle; }
    }

    @media (--md-up) {
      font-size: 1.5rem;
      margin-top: .25rem;
    }
  }

  &__cancel {
    color: rgba(0, 0, 0, .35) !important;
    cursor: pointer;
    font-size: 1.6rem;
    position: absolute;
    right: .5rem;
    top: .5rem;

    &::before {
      @extend %icon;
      @extend %icon-cancel;
    }
  }
}
