/* stylelint-disable */

.t-docs {
  .section {
    border-bottom: 1px solid var(--grey-300);
    padding-bottom: 2em;
  }

  ::-moz-selection { background: var(--gold-300); }
  ::selection { background: var(--gold-300); }

  .example {
    border: var(--grey-500) 1px solid;
    padding: 1em;
    position: relative;

    > * {
      position: relative !important;
    }
  }

  code,
  .hljs *:not(.hljs-rules) {
    color: var(--red-600) !important;
  }

  pre code {
    display: block;
    overflow: auto;
    padding: 1em;
  }

  code {
    background: #fffaf0;
    font-size: 115%;
    @media (--sm) {
      white-space: nowrap;
    }
  }

  .box-row {
    margin-bottom: 1rem;
  }

  .box,
  .box-first,
  .box--large,
  .box-nested,
  .box-row {
    background-color: var(--blue-600);
    color: var(--white);
    min-height: 2rem;
    overflow: hidden;
    padding: .75rem;
    position: relative;
    text-align: center;
    font-size: .8rem;
    i {
      position: relative;
      top: 3px;
    }
  }

  .box-first {
    background-color: var(--blue-700);
  }

  .box-nested {
    background-color: var(--blue-800);
  }

  .box--large {
    height: 5rem;
  }

  .icon-example {
    li {
      display: inline-block;
      list-style: none;
      overflow: hidden;
      margin: 0 35px 10px 0;
      padding: 0;
      width: 25%;
    }
    i {
      font-size: 26px;
      float: left;
    }
    code {
      width: 250px;
      display: block;
      float: right;
    }
  }
}

/* stylelint-enable */

