:root {
  --gutter-width: var(--global-gutter-width);
  --outter-margin: var(--global-outter-margin);;
  --gutter-compensation: calc((var(--gutter-width) * .5) * -1);
  --half-gutter-width: calc((var(--gutter-width) * .5));
  --sm-min: var(--global-small-media-min);
  --md-min: var(--global-medium-media-min);
  --lg-min: var(--global-large-media-min);
  --container-sm: calc(var(--sm-min) + var(--gutter-width));
  --container-md: calc(var(--md-min) + var(--gutter-width));
  --container-lg: calc(var(--lg-min) + var(--gutter-width));
  --col-1: 8.333%;
  --col-2: 16.666%;
  --col-3: 25%;
  --col-4: 33.333%;
  --col-5: 41.667%;
  --col-6: 50%;
  --col-7: 58.33%;
  --col-8: 66.667%;
  --col-9: 75%;
  --col-10: 83.333%;
  --col-11: 91.667%;
  --col-12: 100%;
}
/* stylelint-enable */

/* Layout */

.u-layout,
%layout {
  display: flex;
  flex-direction: column;
}

/* Containers */

.u-container--flex,
%container-flex {
  @extend %reset-box;
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
}

.u-container,
%container {
  margin-left: auto;
  margin-right: auto;
}

.u-container--fluid,
%container-fluid {
  @extend %container;
  padding-left: var(--outter-margin, 2rem);
  padding-right: var(--outter-margin, 2rem);
}

/* Row */

.u-row,
%row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: var(--gutter-compensation, -.5rem);
  margin-right: var(--gutter-compensation, -.5rem);
}

.u-row--reverse,
%row-reverse { flex-direction: row-reverse; }

.u-first,
.u-column--first,
%first { order: -1; }

.u-last,
.u-column--last,
%last { order: 1; }

.u-row--break,
%row-break {
  margin-left: -1rem;
  margin-right: -1rem;
}


/* Columns */

%column-base {
  box-sizing: border-box;
  flex: 0 0 auto;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  padding-left: var(--half-gutter-width, .5rem);
  padding-right: var(--half-gutter-width, .5rem);
}

@define-mixin column $width {
  @extend %column-base;
  flex-basis: $width;
  max-width: $width;
}

.u-column,
%column {
  @extend %column-base;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.u-column--reverse, %column-reverse { flex-direction: column-reverse; }

