@charset "UTF-8";

@font-face {
  font-family: "navis-icons";
  font-style: normal;
  font-weight: normal;
  src: url("fonts/navis-icons.eot");
  src: url("fonts/navis-icons.eot?#iefix") format("embedded-opentype"),
    url("fonts/navis-icons.woff") format("woff"),
    url("fonts/navis-icons.ttf") format("truetype"),
    url("fonts/navis-icons.svg#navis-icons") format("svg");
}

[data-icon]::before {
  content: attr(data-icon);
  font-family: "navis-icons" !important;
  font-style: normal !important;
  font-variant: normal !important;
  font-weight: normal !important;
  line-height: 1;
  speak: none;
  text-transform: none !important;
}

%icon,
[class^="o-icon-"]::before,
[class*=" o-icon-"]::before {
  font-family: "navis-icons" !important;
  font-style: normal !important;
  font-variant: normal !important;
  font-weight: normal !important;
  line-height: 1;
  speak: none;
  text-transform: none !important;
}

%icon-user, .o-icon-user::before { content: "a"; }
%icon-user-add, .o-icon-user-add::before { content: "b"; }
%icon-users, .o-icon-users::before { content: "c"; }
%icon-check-mark, .o-icon-check-mark::before { content: "d"; }
%icon-check-mark-circle, .o-icon-check-mark-circle::before { content: "e"; }
%icon-x-mark-circle, .o-icon-x-mark-circle::before { content: "f"; }
%icon-x-mark, .o-icon-x-mark::before { content: "g"; }
%icon-cog, .o-icon-cog::before { content: "h"; }
%icon-search, .o-icon-search::before { content: "i"; }
%icon-inbox, .o-icon-inbox::before { content: "j"; }
%icon-star, .o-icon-star::before { content: "k"; }
%icon-help-circle, .o-icon-help-circle::before { content: "l"; }
%icon-key, .o-icon-key::before { content: "m"; }
%icon-block, .o-icon-block::before { content: "n"; }
%icon-calendar, .o-icon-calendar::before { content: "o"; }
%icon-clock, .o-icon-clock::before { content: "q"; }
%icon-reload-ccw, .o-icon-reload-ccw::before { content: "r"; }
