/*---
 section: Navigation
 title: Bar
 ---

 Use the class `.c-bar` on a `<div>` or `<header>` to get an application style top bar.

 ``` html_example
 <header class="c-bar">
    <nav>
      <ul>
        <li><h1 class="o-brand">Title</h1></li>
        <li><i class="o-icon-right-dir"></i> <h2>Item</h2></li>
      </ul>
    </nav>
    <nav>
      <ul>
        <li><a href="#"><i class="o-icon-help-circle"></i></a></li>
      </ul>
    </nav>
 </header>
 ```

 <br><br>

 ```html_example
 <div class="u-row">
   <div class="u-column--6@md">
     <header class="u-orange-600 c-bar">
        <nav>
          <ul><li><a href="#"><i class="o-icon-help-circle"></i></a></li></ul>
        </nav>
        <nav>
          <ul>
            <li><h2>Reverse Bar</h2></li>
          </ul>
        </nav>
     </header>
     </div>

     <div class="u-column--6@md">
     <header class="c-bar u-red-600">
        <nav>
          <ul><li><h2>Half Bar</h2></li></ul>
        </nav>
        <nav>
          <ul><li><a href="#"><i class="o-icon-star"></i></a></li></ul>
        </nav>
     </header>
   </div>
 </div>
 ```

 */

@define-mixin bar $background, $border-color: transparent, $border-width: 0 0 2px {
  @extend %row;
  background-color: $background;
  border-width: $border-width;
  border-style: solid;
  border-color: $border-color;
  color: contrast($background);
  max-height: 58px;
  min-height: 58px;
  padding: 0 1em;
  z-index: 100;

  > a:not([class]),
  i {
    color: contrast($background);
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    height: 46px;
  }

  h2 {
    padding-left: 2.75rem;
    font-size: 1.18rem;
    padding-top: 1.3rem;
  }

  > :last-child > ul {
    justify-content: flex-end;

    > li {
      margin-left: 10px;

      @media (--md-up) {
        margin-left: 30px;
      }
    }
  }

  > :first-child > ul {
    justify-content: flex-start;

    > li {
      margin-right: 10px;

      @media (--md-up) {
        margin-right: 30px;
      }
    }
  }

  nav {
    @extend %column;
    padding: .5em 0;

    > ul {
      @extend %middle;
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      height: 40px;

      > li {
        display: inline-block;

        i {
          position: relative;
          top: 5px;
        }
      }

      h2,
      h3,
      h4,
      h5,
      h6 {
        display: inline;
        font-size: 1.25em;
        font-weight: 100;
        margin-right: 30px;
        margin-top: 0;
        overflow: hidden;
        position: relative;
        text-transform: uppercase;
        padding: 0;
      }
    }
  }

  i {
    font-size: 25px;
  }
}

%bar-base {
  @mixin bar var(--primary-color), transparent;
}

.c-bar {
  @extend %bar-base;

  &--top {
    @extend %sticky-top;
    @extend %bar-base;

    .t-docs & {
      @mixin bar var(--red-600), transparent;
    }

    .t-insight & {
      @mixin bar var(--primary-color), var(--secondary-color);
    }

  }

  &--bottom {
    @mixin bar var(--background-color), var(--grey-300), 2px 0 0;
    padding-top: 1em;
  }
}

.o-brand {
  background-image: url('img/navis-logo--inverted.svg');
  background-repeat: no-repeat;
  background-size: contain;
  width: 90px;
  text-indent: -99999em;
  position: relative;
  margin-top: 10px !important;
  margin-bottom: 0;

  .t-docs & {
    background-image: url('img/navis-ui-logo.svg');
  }
}


