/*---
 section: Navigation
 title: Subnav
 ---

 A horizontal list of options using a `<ul>`.

 #### Horizontal

 ```html_example
 <ul class="c-subnav--horizontal">
   <li><a href="#" class="c-subnav--active">Link 1</a></li>
   <li><a href="#">Link 2</a></li>
   <li><a href="#">Link 3</a></li>
 </ul>
 ```

 #### Vertical

 ```html_example
 <ul class="c-subnav--vertical">
   <li><a href="#" class="c-subnav--active">Link 1</a></li>
   <li><a href="#">Link 2</a></li>
   <li><a href="#">Link 3</a></li>
 </ul>
 ```
 */

%subnav-active {
  border-bottom: var(--blue-800) 2px solid !important;
  color: var(--blue-800);
}

%subnav {
  @extend %no-bullets;

  a {
    padding: .5rem 1rem;
    border-bottom: transparent 2px solid;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition-duration: var(--global-duration);
    &:not([class]) {
      color: var(--grey-700);
    }

    &:hover,
    &:focus {
      border-bottom: var(--blue-400) 2px solid;
      text-decoration: none !important;
    }

    &:active {
      @extend %subnav-active;
    }
  }
}

.c-subnav {
  @extend %subnav;

  &--active {
    @extend %subnav-active;
  }

  &--horizontal {
    @extend %list-horizontal;
    @extend %list-inline;
    @extend %subnav;
  }

  &--vertical {
    @extend %subnav;
    a {
      display: inline-block;
    }
  }
}
