/*---
 section: Base
 title: Images
 ---

 ### Icons

 Usage `<i class="o-icon-user"></i>`

 */

@import "../fonts/navis-icons";

:root {
  --avatar-small: 40px;
  --avatar-medium: 75px;
  --avatar-large: 150px;
  --avatar-xlarge: 250px;
}

@define-mixin avatar $size {
  border-radius: 5px;
  display: inline-block;
  height: $size;
  margin: 5px;
  width: $size;
}

@define-mixin icon $size, $color {
  font-size: $size;
  color: $color;
}

.t-default {
  img { width: 100%; }
}

.o-avatar {
  &--default {
    background-color: var(--grey-300);
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: 50%;
    /* background-image: svg("md/svg-sprite-social#ic_person_24px", "[fill]: var(--white)"); */

    [src=""] { display: none; }
  }

  &--small {
    @mixin avatar var(--avatar-small);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-small); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-small); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-small); } }
  }

  &--medium {
    @mixin avatar var(--avatar-medium);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-medium); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-medium); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-medium); } }
  }

  &--large {
    @mixin avatar var(--avatar-large);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-large); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-large); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-large); } }
  }

  &--xlarge {
    @mixin avatar var(--avatar-xlarge);

    @media (--sm-up) { &\@sm { @mixin avatar var(--avatar-xlarge); } }
    @media (--md-up) { &\@md { @mixin avatar var(--avatar-xlarge); } }
    @media (--lg-up) { &\@lg { @mixin avatar var(--avatar-xlarge); } }
  }

}


/** @define o-icon */

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local('Material Icons'), local('../fonts/MaterialIcons-Regular'), url(.woff2) format('woff2');
  /* src: local('Material Icons'), local('MaterialIcons-Regular'), url(https://fonts.gstatic.com/s/materialicons/v8/2fcrYFNaTjcS6g4U3t-Y5UEw0lE80llgEseQY3FEmqw.woff2) format('woff2'); */
}

.o-icon {
  direction: ltr;
  display: inline-block;
  font-family: 'Material Icons';
  font-feature-settings: 'liga';
  font-weight: normal;
  font-smoothing: antialiased;
  font-size: 24px;
  font-style: normal;
  height: 1em;
  letter-spacing: normal;
  line-height: 1;
  text-rendering: optimizeLegibility;
  text-transform: none;
  white-space: nowrap;
  width: 1em;
  word-wrap: normal;

  &--hero {
    @mixin icon 90px, var(--blue-400);
    display: block;
    margin: .25em 0 -.5em;

    @media (--md-up) {
      font-size: 70px;
      margin: .5em 0 0;
    }
  }
}

